import React, { memo, useEffect, useState } from 'react';
import * as io from 'socket.io-client';
import { Frame } from '@piloteers/dashboard-model';
import { User } from '@piloteers/dashboard-model';
import { RolesEnum } from '@piloteers/dashboard-model';
import { FrameTypesEnum } from '@piloteers/dashboard-model';
import * as uuid from 'uuid/v1';
import "./app.styles.scss";
import Clock from 'react-live-clock';

const ENV = require('environment');
declare var window: any;

const userId = localStorage.getItem('userId') || uuid();
var socket = io(ENV.SOCKET_URL, { query: `userId=${userId}&role=${RolesEnum.PI}` });

let frameNumber = 0;
let timeoutSlider;
let timeoutFrame;
let reconnectInterval;

export class App extends React.PureComponent<any, any> {

  user = null;

  constructor(props) {
    super(props)
    this.state = {
      closeScreen: false,
      activeFrame: null,
      openScreen: false,
      frames: [],
      updatingVersion: false,
    }

    localStorage.setItem('userId', userId);

    socket.on('connect', () => {
      if (reconnectInterval) { clearInterval(reconnectInterval); }
      console.log('socket: connected');
    });

    socket.on('disconnect', () => {
      console.log('socket: disconnected');
      reconnectInterval = setInterval(() => {
        socket.connect();
      }, 1000);
    });

    socket.on('frame:list', (f: Frame[]) => {
      console.log(f);
      frameNumber = 0;
      this.setState({ ...this.state, frames: f, activeFrame: null }, () => {
        this.newFrame();
      });

      if (f.length == 0) {
        this.setState({ ...this.state, closeScreen: true, activeFrame: null })
      }
    })

    socket.on('frame:sync', (f: Frame[]) => {
      frameNumber = 0;
      this.setState({ ...this.state, frames: f, activeFrame: null });
      this.newFrame();
    })


    socket.on('user:me', (u: User) => {
      console.log(u);
      this.user = u;
      socket.emit('frame:get');
    })

    socket.on('update:show', (version) => {
      console.log('update:show', version);
      this.setState({ updatingVersion: true })
    })
    socket.emit('user:get:me');

  }

  newFrame() {
    const { activeFrame, frames } = this.state;
    if (timeoutFrame) { clearTimeout(timeoutFrame) }
    if (timeoutSlider) { clearTimeout(timeoutSlider) }
    if (activeFrame == null && frames.length) {

      this.setState({
        ...this.state,
        activeFrame: frames[frameNumber],
        openScreen: frames[frameNumber].type == FrameTypesEnum.WEBSITE ? false : true
      }, () => {

        timeoutFrame = setTimeout(() => {
          this.setState({ ...this.state, closeScreen: true, openScreen: false }, () => {

            timeoutSlider = setTimeout(() => {

              this.setState({ ...this.state, closeScreen: false, activeFrame: null }, () => {

                if (frameNumber + 1 >= frames.length) {
                  frameNumber = 0;
                } else {
                  frameNumber++;
                }
                this.newFrame()
              });

            }, 1000);
          });


        }, frames[frameNumber].time * 1000); // to seconds
      });


    }
  }

  onload() {
    const { activeFrame } = this.state;
    console.log('loaded');
    if (activeFrame) {

      this.setState({
        ...this.state,
        openScreen: true,
      })
    }
  }


  render() {
    const { closeScreen, openScreen, activeFrame, updatingVersion } = this.state;

    return (
      <div className="plt-app">
        {/* <InstaImage
          src={`http://d3j5vin7wusm92.cloudfront.net/images/header.jpg`}
        /> */}
        <div className="plt-clock">
          <Clock format={'HH:mm'} ticking={true} />
        </div>
        {
          updatingVersion ?
            <div className="plt-update">
              <div>
                <h3>Updating</h3>
                <p>Updating to the newest dashboard version: {window.version ? window.version : "0.0.0"}</p>
                <p>This can take a few minutes...</p>
                <svg width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid"><circle cx="64.5198" cy="65.4802" r="5" fill="#3be8b0">
                  <animate attributeName="cx" values="80;50" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                  <animate attributeName="cy" values="50;80" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                  <animate attributeName="fill" values="#3be8b0;#1aafd0" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                </circle><circle cx="34.5198" cy="64.5198" r="5" fill="#1aafd0">
                    <animate attributeName="cx" values="50;20" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="cy" values="80;50.00000000000001" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="fill" values="#1aafd0;#6a67ce" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                  </circle><circle cx="35.4802" cy="34.5198" r="5" fill="#6a67ce">
                    <animate attributeName="cx" values="20;49.99999999999999" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="cy" values="50.00000000000001;20" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="fill" values="#6a67ce;#7cfc63" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                  </circle><circle cx="65.4802" cy="35.4802" r="5" fill="#7cfc63">
                    <animate attributeName="cx" values="49.99999999999999;80" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="cy" values="20;49.99999999999999" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                    <animate attributeName="fill" values="#7cfc63;#3be8b0" keyTimes="0;1" dur="1s" repeatCount="indefinite"></animate>
                  </circle>
                </svg>
              </div>
            </div>
            : null
        }
        <div className="plt-pi-id">{window.version ? window.version : "0.0.0"}: {userId}</div>
        <div className={`plt-screen 
          ${openScreen ? 'plt-screen--open' : ''}  
          ${closeScreen ? 'plt-screen--close' : ''}  
          ${ activeFrame && activeFrame.type == FrameTypesEnum.BREAK ? 'plt-screen--break' : ''}`
        }>

          <iframe onLoad={() => this.onload()} src={activeFrame ? activeFrame.url : null}> </iframe>

        </div>
      </div>
    )
  }
}